import React from 'react';
import NavigationButton, {Paths} from "./NavigationButton";
import "./header.css"
import icon from "./icon.png"
import {useTranslation} from "react-i18next";
import { Link } from 'react-router-dom';

const Header = () => {
    const {t} = useTranslation();
    return (
        <header>
            <div className="background-line">
                <div className="container">
                    <Link to="" className="container"><NavigationButton info={{path: Paths.Home, label: t('home')}}/></Link>
                    <Link to="About" className="container"><NavigationButton info={{path: Paths.About, label: t('about')}}/></Link>
                    <Link to="Family" className="container"><NavigationButton info={{path: Paths.Home, label: t('family-btn')}}/></Link>
                    <Link to="Developers" className="container"><NavigationButton info={{path: Paths.Home, label: t('developers')}}/></Link>
                </div>
            </div>
            <img className="header_icon" src={icon}/>
        </header>
    );
};

export default Header;